import styled from 'styled-components';
import { Form, Modal } from 'react-bootstrap'
import RowSpacer from '../../../../common/styles/RowSpacer';
import AddButton from '../../../../common/button/AddButton';
import { booksitoutServer } from '../../../../config/booksitoutServer';
import { useParams } from 'react-router-dom';
import { useState } from 'react';
import toast from 'react-hot-toast';

interface Props {
    isOpen: boolean
    close: () => void
}

const BookDetailMemoAddModal: React.FC<Props> = ({ isOpen, close }) => {
    const { bookId } = useParams()

    const [page, setPage] = useState<number | null>(null)
    const [content, setContent] = useState<string>('')

    const addMemo = () => {
        if (content === '')  {
            toast.error('  .')
            return
        }

        const data = {
            page: page,
            content: content
        }

        booksitoutServer
            .post(`/v1/book/${bookId}/memo`, data)
            .then(() => {
                toast.success(' .')
                close()
                window.location.reload()
            })
            .catch(() => toast.error(' .     .'))
    }

    return (
        <Modal show={isOpen} onHide={close} centered fullscreen='md-down' backdrop='static'>
            <Modal.Header closeButton>
                <Title>  </Title>
            </Modal.Header>

            <Modal.Body>
                <Form>
                    <Form.Group>
                        <Form.Control 
                            type="number" 
                            placeholder=" " 
                            onChange={(e) => setPage(Number(e.target.value))}
                        />
                    </Form.Group>

                    <RowSpacer size={10} />

                    <Form.Group>
                        <Form.Control 
                            as="textarea" 
                            placeholder=" " 
                            style={{ height: '200px' }} 
                            onChange={(e) => setContent(String(e.target.value))}
                        />
                    </Form.Group>

                    <RowSpacer size={10} />

                    <AddButton label={''} onClick={addMemo} />
                </Form>
            </Modal.Body>
        </Modal>
    )
}

const Title = styled.h2`
    text-align: center;
`;

export default BookDetailMemoAddModal